@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.alert-div {
    display: block;
    margin: auto;
    width: auto;
}

.create-game-page {
    width: 100%;
    height: 100%;
}

.create-game-body {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 70vh;
}

.content-div {
    width: 33%;
    height: 75%;
    background-color: #0a0d14;
    display: inline-block;
    border-radius: 10%;
    border: .25vh solid #353b48;
    overflow: hidden;
}

.content-div:hover {
    cursor: pointer;
    border: .25vh solid white;
}

.content-div:hover h3 {
    color: #0984e3;
    transform: scale(1.25);
    transition: .5s ease-in-out;
}

.content-create-div {
    display: none;
    width: 100%;
}

.content-div:hover img{
    opacity: 10%;
}
 
.form-div {
    display: inline-block;
    width: 100%;
    height: 100vh;
    text-align: center;
}

.form-row {
    width: 100%;
}

.form-input {
    display: inline-flex;
    font-family: "Audiowide";
    justify-content: center;
    width: auto;
    color: white;
    border-radius: 1vh;
    padding: 1vh;
    background-color: #ffffff;
    margin: 1vh;
}

.form-row h2 {
    color: #0984e3;
}

img {
    width: 100%;
    height: 100%;

}

.header {
    width: 100%;
    height: auto;
    display: inline-block;
    justify-content: center;
    align-items: center;
    color: white;
    border-bottom: .25vh solid #0984e3;
    max-height: 15%;
    overflow: hidden;
}

.header h3 {
    margin-top: 1vh;
    display: inline-flex;
    justify-content: center;
    width: 100%;
    text-align: center;
}

.header p {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    font-size: 100%;
    font-style: italic;
    color: grey;
    text-align: center;
}

.divider {
    margin: 2.5vh;
    border-bottom: .1vh solid #0984e3;
}

.create-game-footer {
    width: 100%;
    height: 5vh;
    display: inline-flex;
    justify-content: center;

}

.create-game-footer a {
    color: #0984e3;
    font-size: 2.5vh;
}

@media (max-width: 800px) {
    .create-game-body {
        display: inline-block;
        text-align: center;
    }

    .content-div {
        width: 80%;
        text-align: center;
        margin-top: 5vh;
        height: auto;
    } 
  }